<template>
  <div scope="row" class="text-center text-muted">{{(index+1).toString().padStart(Math.max(2,todoL), '0')}}</div>
  <div class="form-check">
    <input class="form-check-input pointer" @click="save(index, $event)" :checked="item.done?true:undefined"
      :id="'input-' + index" type="checkbox" value="" />
    <label class="form-check-label pointer" :class="[{'text-decoration-line-through': item.done === true}, item.status==='z'?'low':item.status]"
      :for="'input-' + index">{{ item.todo}}</label>
  </div>
  <div class="flex-fill pointer" :class="item.status==='z'?'low':item.status" @click="statusItem(index)">{{item.status==='z'?'low':item.status}}</div>
  <div class="flex-fill pointer text-center" @click="editItem(index)">edit</div>
  <div class="flex-fill pointer text-center trash text-danger" @click="removeItem(index)">delete</div>
  <div class="flex-fill pointer text-center" @click="infoItem(index)">info</div>
</template>
<script>
  export default {
    name: 'listItem',
    emits: ['priority', 'remove', 'edit', 'local', 'info'],
    props: {
      todoL: Number,
      item: Object,
      index: Number
    },
    methods: {
      statusItem(index) {
        this.$emit('priority', index)
      },
      removeItem(index) {
        this.$emit('remove', index)
      },
      editItem(index) {
        this.$emit('edit', index)
      },
      save(index, e) {
        this.$emit('local', index, e)
      },
      infoItem(index) {
        this.$emit('info', index)
      }
    }
  }
</script>
<style scoped>
  .low {
    color: #497195;
  }

  .mid {
    color: #7b6e0a;
  }

  .hight {
    color: #b5495f;
  }
</style>